import React, { useState } from 'react';
import { HashRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import Layout from './components/layout/Layout';
import HomePage from './components/pages/HomePage';
import ServicesPage from './components/pages/ServicesPage';
import CaseStudiesPage from './components/pages/CaseStudiesPage';
import AboutPage from './components/pages/AboutPage';
import ContactPage from './components/pages/ContactPage';
import ProcessPage from './components/pages/ProcessPage';
import ThankYouPage from './components/pages/ThankYouPage';
import DebugPage from './components/pages/DebugPage';

// Get current date and time if in development, or build timestamp if in production
const getCurrentDateTime = () => {
  return new Date().toLocaleString('en-US', {
    timeZone: 'America/New_York',
    weekday: 'long',
    year: 'numeric',
    month: 'long',
    day: 'numeric',
    hour: '2-digit',
    minute: '2-digit',
    second: '2-digit',
    hour12: true
  });
};

const buildTimestamp = process.env.REACT_APP_BUILD_TIME || getCurrentDateTime();

const App = () => {
  const [activePage, setActivePage] = useState('Home');

  return (
    <Router>
      <Routes>
        <Route element={<Layout setActivePage={setActivePage} buildTimestamp={buildTimestamp} />}>
          <Route index element={<HomePage setActivePage={setActivePage} />} />
          <Route path="services" element={<ServicesPage setActivePage={setActivePage} />} />
          <Route path="cases" element={<CaseStudiesPage setActivePage={setActivePage} />} />
          <Route path="about" element={<AboutPage setActivePage={setActivePage} />} />
          <Route path="contact" element={<ContactPage setActivePage={setActivePage} />} />
          <Route path="process" element={<ProcessPage setActivePage={setActivePage} />} />
          <Route path="thank-you" element={<ThankYouPage setActivePage={setActivePage} />} />
          <Route path="debug" element={<DebugPage setActivePage={setActivePage} />} />
          <Route path="*" element={<Navigate to="/" replace />} />
        </Route>
      </Routes>
    </Router>
  );
};

export default App;
