  const zIndices = {
    center: 100,
    clinical: 70,
    records: 50,
    insurance: 10,
    coordination: 40,
    support: 5,
    coaching: 60
  };

  // 2) Increased orbitRadiusPercent to spread out circles
  const getCircleStyle = (id, color) => {
    const { width, height } = containerSize;
    if (!width || !height) {
      // If container not measured yet, just hide circles
      return { display: 'none' };
    }

    const minDim = Math.min(width, height);
    // const maxAllowedDim = Math.min(minDim, window.innerHeight * 0.5);

    // Outer circle size = 1/4 of min dimension (reduced from 1/3)
    const circleSize = minDim / 3;
    // Center circle size = 3/4 of that
    const centerSize = circleSize;

    // Increase radius => 50 => 50% of (minDim/2)
    const orbitRadiusPercent = 60; // Reduced from 55 to bring circles closer

    const angleDeg = circleAngles[id] || 0;
    const angleRad = (Math.PI / 180) * angleDeg;

    const isCenter = id === 'center';
    const diameter = isCenter ? centerSize : circleSize;

    const centerX = width / 2;
    const centerY = height / 2;

    let x = centerX;
    let y = centerY;

    if (!isCenter) {
      // radiusPx = 0.40 * (minDim/2)
      const radiusPx = (orbitRadiusPercent / 100) * (minDim / 2);
      x = centerX + radiusPx * Math.cos(angleRad);
      y = centerY + radiusPx * Math.sin(angleRad);
    }

    const baseStyle = {
      position: 'absolute',
      width: `${diameter}px`,
      height: `${diameter}px`,
      left: `${x}px`,
      top: `${y}px`,
      transform: 'translate(-50%, -50%)',
      borderRadius: '9999px',
      backgroundColor: color,
      zIndex: hoveredCircle === id || selectedCircle === id ? 200 : zIndices[id] || 10,
      cursor: 'pointer',
      transition: 'all 0.3s ease-out',
      boxShadow: hoveredCircle === id || selectedCircle === id
        ? '0 20px 25px -5px rgba(0, 0, 0, 0.15), 0 8px 10px -6px rgba(0, 0, 0, 0.1)'
        : '0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06)',
      scale: selectedCircle === id ? '1.1' : hoveredCircle === id ? '1.05' : '1'
    };

    return baseStyle;
  };

