import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { 
  ClipboardCheck, Users, Brain, 
  PhoneCall, ArrowRight, CheckCircle2,
  Stethoscope, HeartPulse, Shield, MessageCircle
} from 'lucide-react';

const ProcessPage = ({ setActivePage }) => {
  const navigate = useNavigate();
  const location = useLocation();
  const [expandedStep, setExpandedStep] = useState(null);

  useEffect(() => {
    setActivePage('process');
    // Only scroll to top if it's a forward navigation (not back/forward)
    if (!location.key) {
      window.scrollTo(0, 0);
    }
  }, [setActivePage, location]);

  const handleNavigation = (path, name) => {
    // Store current scroll position before navigating
    const scrollPosition = window.scrollY;
    sessionStorage.setItem('scrollPosition_process', scrollPosition.toString());
    
    window.scrollTo(0, 0);
    setActivePage(name);
    navigate(path);
  };

  // Handle scroll restoration on back navigation
  useEffect(() => {
    // Check if we're returning via back/forward navigation
    if (location.key) {
      const savedPosition = sessionStorage.getItem('scrollPosition_process');
      if (savedPosition) {
        window.scrollTo(0, parseInt(savedPosition));
        sessionStorage.removeItem('scrollPosition_process');
      }
    }
  }, [location]);

  const toggleStep = (stepNumber) => {
    setExpandedStep(expandedStep === stepNumber ? null : stepNumber);
  };

  const processSteps = [
    {
      step: 1,
      title: "Initial Consultation",
      icon: PhoneCall,
      description: "We begin with a thorough assessment of your needs and challenges.",
      details: [
        "Comprehensive review of medical history",
        "Discussion of current healthcare challenges",
        "Assessment of immediate and long-term needs",
        "Understanding family dynamics and support system",
        "Identification of primary healthcare goals"
      ]
    },
    {
      step: 2,
      title: "Care Planning",
      icon: Brain,
      description: "Develop a personalized strategy tailored to your specific situation.",
      details: [
        "Creation of detailed care roadmap",
        "Coordination with healthcare providers",
        "Insurance and benefits review",
        "Resource identification and allocation",
        "Timeline development for care milestones"
      ]
    },
    {
      step: 3,
      title: "Implementation",
      icon: ClipboardCheck,
      description: "Execute the plan with continuous coordination and support.",
      details: [
        "Appointment scheduling and coordination",
        "Provider communication management",
        "Documentation organization",
        "Care team assembly and coordination",
        "Regular progress monitoring"
      ]
    },
    {
      step: 4,
      title: "Ongoing Support",
      icon: HeartPulse,
      description: "Provide continuous advocacy and adjust strategies as needed.",
      details: [
        "Regular check-ins and updates",
        "Plan adjustments based on outcomes",
        "Continuous advocacy support",
        "Crisis intervention when needed",
        "Progress evaluation and goal updates"
      ]
    }
  ];

  const benefits = [
    {
      icon: Shield,
      title: "Comprehensive Protection",
      description: "We ensure all aspects of your healthcare journey are covered and protected."
    },
    {
      icon: Users,
      title: "Family Integration",
      description: "We keep your family informed and involved throughout the entire process."
    },
    {
      icon: MessageCircle,
      title: "Clear Communication",
      description: "Regular updates and open channels of communication ensure you're always informed."
    },
    {
      icon: Stethoscope,
      title: "Clinical Excellence",
      description: "Our medical expertise ensures the highest standard of care coordination."
    }
  ];

  return (
    <div className="container mx-auto px-4 py-12">
      <div className="text-center mb-16">
        <h1 className="text-4xl md:text-5xl font-bold mb-6 text-blue-900">Our Process</h1>
        <p className="text-xl text-gray-600 max-w-3xl mx-auto">
          A systematic approach to healthcare navigation, designed to provide you with comprehensive support and guidance every step of the way.
        </p>
      </div>

      {/* Horizontal Stepper */}
      <div className="hidden lg:block mb-16">
        <div className="relative">
          {/* Progress Line */}
          <div className="absolute top-1/2 left-0 right-0 h-1 bg-gray-200 -translate-y-1/2">
            <div 
              className="h-full bg-blue-600 transition-all duration-500"
              style={{ 
                width: expandedStep === null ? '0%' : `${expandedStep * 32.5 + 2.5}%`
              }}
            />
          </div>

          {/* Steps */}
          <div className="relative flex justify-between">
            {processSteps.map((step, index) => (
              <div 
                key={index}
                className="flex flex-col items-center cursor-pointer group"
                onClick={() => toggleStep(index)}
              >
                {/* Step Circle */}
                <div 
                  className={`w-16 h-16 rounded-full flex items-center justify-center relative z-10 transition-all duration-500 ${
                    expandedStep !== null && index <= expandedStep
                      ? 'bg-blue-600 text-white scale-110'
                      : 'bg-white text-blue-600 border-2 border-blue-600'
                  } ${
                    expandedStep === index ? 'ring-4 ring-blue-200' : ''
                  }`}
                >
                  <step.icon className="w-8 h-8" />
                </div>

                {/* Step Title */}
                <div className="mt-4 text-center">
                  <div className={`font-semibold mb-1 transition-colors duration-300 ${
                    expandedStep !== null && index <= expandedStep ? 'text-blue-900' : 'text-gray-600'
                  }`}>
                    Step {step.step}
                  </div>
                  <div className={`text-sm transition-colors duration-300 ${
                    expandedStep !== null && index <= expandedStep ? 'text-blue-800' : 'text-gray-500'
                  }`}>
                    {step.title}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Mobile Stepper */}
      <div className="lg:hidden mb-16">
        <div className="relative flex flex-col space-y-8">
          {/* Vertical Progress Line */}
          <div className="absolute top-0 bottom-0 left-8 w-1 bg-gray-200">
            <div 
              className="w-full bg-blue-600 transition-all duration-500"
              style={{ 
                height: expandedStep === null ? '0%' : `${10 + expandedStep * 30}%`
              }}
            />
          </div>

          {/* Steps */}
          {processSteps.map((step, index) => (
            <div 
              key={index}
              className="relative flex items-start cursor-pointer group"
              onClick={() => toggleStep(index)}
            >
              {/* Step Circle */}
              <div 
                className={`w-16 h-16 rounded-full flex items-center justify-center relative z-10 transition-all duration-500 ${
                  expandedStep !== null && index <= expandedStep
                    ? 'bg-blue-600 text-white scale-110'
                    : 'bg-white text-blue-600 border-2 border-blue-600'
                } ${
                  expandedStep === index ? 'ring-4 ring-blue-200' : ''
                }`}
              >
                <step.icon className="w-8 h-8" />
              </div>

              {/* Step Title */}
              <div className="ml-6">
                <div className={`font-semibold mb-1 transition-colors duration-300 ${
                  expandedStep !== null && index <= expandedStep ? 'text-blue-900' : 'text-gray-600'
                }`}>
                  Step {step.step}
                </div>
                <div className={`text-sm transition-colors duration-300 ${
                  expandedStep !== null && index <= expandedStep ? 'text-blue-800' : 'text-gray-500'
                }`}>
                  {step.title}
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Process Steps */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8 mb-16">
        {processSteps.map((step, index) => (
          <div 
            key={index} 
            className="relative"
          >
            {/* Arrow container - positioned based on initial box height */}
            {index < 3 && (
              <div className="hidden lg:block absolute right-0 z-50" style={{ top: '116px' }}>
                <div className="w-12 h-12 flex items-center justify-center transform translate-x-1/2">
                  <ArrowRight className="w-12 h-12 text-blue-600" />
                </div>
              </div>
            )}
            <div 
              className={`bg-white p-8 rounded-xl shadow-lg cursor-pointer transition-all duration-500 ${
                expandedStep === index ? 'ring-2 ring-blue-500 transform scale-[1.02] shadow-xl' : 'hover:scale-[1.02] hover:shadow-xl'
              }`}
              onClick={() => toggleStep(index)}
            >
              <div className="flex flex-col items-start mb-6">
                <div className="flex items-center justify-between w-full mb-6">
                  <div className="w-16 h-16 bg-blue-600 text-white rounded-full flex items-center justify-center text-2xl font-bold transform transition-all duration-500 group-hover:scale-110">
                    {step.step}
                  </div>
                  <div className="w-8 h-8 flex items-center justify-center transform transition-all duration-500 group-hover:scale-110">
                    <step.icon className="w-8 h-8 text-blue-600" />
                  </div>
                </div>
                <h3 className="text-2xl font-semibold mb-4 text-blue-900">{step.title}</h3>
                <p className="text-gray-600 mb-4 leading-relaxed">{step.description}</p>
                <div className="flex items-center text-blue-600 font-medium">
                  <span className="mr-2">{expandedStep === index ? 'Show Less' : 'Learn More'}</span>
                  <svg
                    className={`w-5 h-5 transform transition-transform duration-300 ${
                      expandedStep === index ? 'rotate-180' : ''
                    }`}
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M19 9l-7 7-7-7" />
                  </svg>
                </div>
              </div>
              
              {/* Expandable Details */}
              <div className={`overflow-hidden transition-all duration-500 ${
                expandedStep === index ? 'max-h-[500px] opacity-100' : 'max-h-0 opacity-0'
              }`}>
                <div className="pt-4 space-y-4">
                  {step.details.map((detail, detailIndex) => (
                    <div key={detailIndex} className="flex items-start space-x-3 group">
                      <div className="p-1 rounded-full group-hover:bg-blue-100 transition-colors duration-300">
                        <CheckCircle2 className="w-5 h-5 text-blue-600 flex-shrink-0 transform transition-all duration-300 group-hover:scale-110" />
                      </div>
                      <span className="text-gray-700 group-hover:text-blue-600 transition-colors duration-300">{detail}</span>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Benefits Section */}
      <div className="bg-gradient-to-br from-blue-50 to-white p-12 rounded-2xl shadow-lg mb-16">
        <h2 className="text-3xl font-bold mb-12 text-center text-blue-900">Benefits of Our Process</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
          {benefits.map((benefit, index) => (
            <div 
              key={index}
              className="group bg-white p-8 rounded-xl shadow-lg transform transition-all duration-500 hover:scale-[1.02] hover:shadow-xl"
            >
              <div className="w-16 h-16 flex items-center justify-center mx-auto mb-6 transform transition-all duration-500 group-hover:scale-110">
                <benefit.icon className="w-12 h-12 text-blue-600" />
              </div>
              <h3 className="text-xl font-semibold mb-4 text-center text-blue-900 group-hover:text-blue-800 transition-colors duration-300">{benefit.title}</h3>
              <p className="text-gray-600 text-center leading-relaxed">{benefit.description}</p>
            </div>
          ))}
        </div>
      </div>

      {/* Call to Action */}
      <div className="relative overflow-hidden">
        <div className="relative bg-gradient-to-br from-blue-50 to-white p-16 rounded-2xl shadow-lg transform transition-all duration-500 hover:shadow-xl">
          <div className="max-w-4xl mx-auto text-center">
            <h2 className="text-4xl font-bold mb-8 text-blue-900">Ready to Start Your Journey?</h2>
            <p className="text-xl mb-12 text-gray-600 leading-relaxed max-w-3xl mx-auto">
              Contact us today to learn more about how we can support your healthcare journey.
            </p>
            <div className="flex flex-col sm:flex-row gap-6 justify-center items-center">
              <button 
                onClick={() => handleNavigation('/contact', 'contact')}
                className="group relative inline-flex items-center justify-center px-10 py-4 font-medium text-white bg-blue-600 rounded-full hover:bg-blue-700 transition-all duration-300 transform hover:scale-105 hover:shadow-lg"
              >
                <span className="relative flex items-center">
                  Schedule a Consultation
                  <svg 
                    className="w-5 h-5 ml-2 transform transition-transform duration-300 group-hover:translate-x-1" 
                    fill="none" 
                    stroke="currentColor" 
                    viewBox="0 0 24 24"
                  >
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M17 8l4 4m0 0l-4 4m4-4H3" />
                  </svg>
                </span>
              </button>
              <button
                onClick={() => handleNavigation('/cases', 'Case Studies')}
                className="inline-flex items-center text-blue-600 hover:text-blue-800 font-semibold text-lg group/btn"
              >
                View Our Case Studies
                <svg 
                  className="w-5 h-5 ml-2 transform transition-transform duration-300 group-hover/btn:translate-x-2" 
                  fill="none" 
                  stroke="currentColor" 
                  viewBox="0 0 24 24"
                >
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M17 8l4 4m0 0l-4 4m4-4H3" />
                </svg>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProcessPage; 