import React, { useState, useEffect } from 'react';
import { Mail, Phone, Send, MapPin, Clock, MessageSquare } from 'lucide-react';

const CONTACT_EMAIL = "xxxx@gmail.com";
const FORMSPREE_ENDPOINT = "https://formspree.io/f/xlddrbon";
const GOOGLE_FORM_ENDPOINT = "https://docs.google.com/forms/d/e/1FAIpQLSf6zYvBDH3wvOqG4B6DEWFd_cqw9JNE469yWwnqPhiP-R_ItQ/formResponse";
const GOOGLE_FORM_FIELDS = {
  name: 'entry.408649125',
  email: 'entry.1597737456',
  message: 'entry.1943926855'
};

const ContactPage = ({ setActivePage }) => {
  const [isSubmitting, setIsSubmitting] = useState(false);

  useEffect(() => {
    setActivePage('contact');
    window.scrollTo(0, 0);
  }, [setActivePage]);

  console.log('setActivePage prop:', setActivePage);

  return (
    <div className="container mx-auto px-4 py-12">
      <div className="text-center mb-16">
        <h2 className="text-4xl md:text-5xl font-bold mb-8 text-blue-900">Contact Us</h2>
        <p className="text-xl text-gray-600 max-w-3xl mx-auto leading-relaxed">
          Let us help you navigate your healthcare journey with confidence.
        </p>
      </div>

      <div className="grid md:grid-cols-2 gap-8 max-w-6xl mx-auto">
        <div className="bg-white p-8 rounded-xl shadow-lg transform transition-all duration-500 hover:scale-[1.02] hover:shadow-xl">
          <h3 className="text-2xl font-semibold mb-8 text-blue-900">Get in Touch</h3>
          <form 
            action={FORMSPREE_ENDPOINT}
            method="POST"
            target="_blank"
            onSubmit={async (e) => {
              e.preventDefault();
              setIsSubmitting(true);
              const form = e.target;
              
              try {
                // Send to Formspree
                const formspreeResponse = await fetch(FORMSPREE_ENDPOINT, {
                  method: 'POST',
                  body: new FormData(form),
                  headers: {
                    'Accept': 'application/json'
                  }
                });

                const formData = new FormData(form);
                const googleFormData = new FormData();
                googleFormData.append(GOOGLE_FORM_FIELDS.name, formData.get('name'));
                googleFormData.append(GOOGLE_FORM_FIELDS.email, formData.get('email'));
                googleFormData.append(GOOGLE_FORM_FIELDS.message, formData.get('message'));

                // Send to Google Forms
                const googleResponse = fetch(
                  GOOGLE_FORM_ENDPOINT,
                  {
                    method: 'POST',
                    body: googleFormData,
                    mode: 'no-cors'
                  }
                );

                const formspreeData = await formspreeResponse.json();
                
                if (formspreeData.ok) {
                  window.location.hash = '#/thank-you';
                } else {
                  console.error('Form submission error:', formspreeData);
                  alert(`Error sending message. Please try again or contact us at ${CONTACT_EMAIL}`);
                }

                // We don't await googleResponse since it's no-cors and will always resolve
                googleResponse.catch(error => {
                  console.error('Google form submission error:', error);
                });
              } catch (error) {
                console.error('Fetch error:', error);
                alert(`Error sending message. Please try again or contact us at ${CONTACT_EMAIL}`);
              } finally {
                setIsSubmitting(false);
              }
            }}
          >
            <div className="space-y-6">
              <div>
                <label className="block mb-2 text-gray-700 font-medium">Name</label>
                <div className="relative group">
                  <input 
                    type="text" 
                    name="name" 
                    className="w-full p-3 pl-12 border rounded-lg transition-all duration-300 
                      focus:ring-2 focus:ring-blue-600 focus:border-transparent
                      group-hover:border-blue-400"
                    required 
                  />
                  <div className="absolute left-3 top-3 transition-all duration-300 group-hover:scale-110 group-hover:text-blue-600">
                    <Mail className="w-5 h-5 text-gray-400 group-hover:text-blue-600" />
                  </div>
                </div>
              </div>
              <div>
                <label className="block mb-2 text-gray-700 font-medium">Email</label>
                <div className="relative group">
                  <input 
                    type="email" 
                    name="email" 
                    className="w-full p-3 pl-12 border rounded-lg transition-all duration-300 
                      focus:ring-2 focus:ring-blue-600 focus:border-transparent
                      group-hover:border-blue-400"
                    required 
                  />
                  <div className="absolute left-3 top-3 transition-all duration-300 group-hover:scale-110 group-hover:text-blue-600">
                    <Mail className="w-5 h-5 text-gray-400 group-hover:text-blue-600" />
                  </div>
                </div>
              </div>
              <div>
                <label className="block mb-2 text-gray-700 font-medium">Message</label>
                <div className="relative group">
                  <textarea 
                    name="message" 
                    className="w-full p-3 pl-12 border rounded-lg h-32 transition-all duration-300 
                      focus:ring-2 focus:ring-blue-600 focus:border-transparent
                      group-hover:border-blue-400" 
                    required
                  ></textarea>
                  <div className="absolute left-3 top-3 transition-all duration-300 group-hover:scale-110 group-hover:text-blue-600">
                    <MessageSquare className="w-5 h-5 text-gray-400 group-hover:text-blue-600" />
                  </div>
                </div>
              </div>
              <button 
                type="submit" 
                disabled={isSubmitting}
                className={`bg-blue-600 text-white px-8 py-4 rounded-full text-lg 
                  hover:bg-blue-700 transition-all duration-300 transform 
                  hover:scale-105 hover:shadow-lg w-full flex items-center justify-center gap-2
                  ${isSubmitting ? 'opacity-50 cursor-not-allowed' : ''}`}
              >
                {isSubmitting ? 'Sending...' : 'Send Message'}
                <Send className={`w-5 h-5 ${isSubmitting ? 'animate-pulse' : ''}`} />
              </button>
            </div>
          </form>
        </div>
        
        <div className="bg-white p-8 rounded-xl shadow-lg transform transition-all duration-500 hover:scale-[1.02] hover:shadow-xl">
          <h3 className="text-2xl font-semibold mb-8 text-blue-900">Contact Information</h3>
          <div className="space-y-6">
            <div className="group bg-blue-50 p-6 rounded-xl transform transition-all duration-300 hover:shadow-md">
              <div className="flex items-center space-x-4">
                <div className="w-12 h-12 rounded-full bg-blue-100 flex items-center justify-center transform transition-all duration-500 group-hover:scale-110 group-hover:bg-blue-200">
                  <Mail className="w-6 h-6 text-blue-600" />
                </div>
                <div>
                  <p className="font-semibold text-blue-900 group-hover:text-blue-800 transition-colors duration-300">Email</p>
                  <p className="text-gray-600">{CONTACT_EMAIL}</p>
                </div>
              </div>
            </div>
            <div className="group bg-blue-50 p-6 rounded-xl transform transition-all duration-300 hover:shadow-md">
              <div className="flex items-center space-x-4">
                <div className="w-12 h-12 rounded-full bg-blue-100 flex items-center justify-center transform transition-all duration-500 group-hover:scale-110 group-hover:bg-blue-200">
                  <Phone className="w-6 h-6 text-blue-600" />
                </div>
                <div>
                  <p className="font-semibold text-blue-900 group-hover:text-blue-800 transition-colors duration-300">Phone</p>
                  <p className="text-gray-600">Contact us to schedule a consultation</p>
                </div>
              </div>
            </div>
            <div className="group bg-blue-50 p-6 rounded-xl transform transition-all duration-300 hover:shadow-md">
              <div className="flex items-center space-x-4">
                <div className="w-12 h-12 rounded-full bg-blue-100 flex items-center justify-center transform transition-all duration-500 group-hover:scale-110 group-hover:bg-blue-200">
                  <MapPin className="w-6 h-6 text-blue-600" />
                </div>
                <div>
                  <p className="font-semibold text-blue-900 group-hover:text-blue-800 transition-colors duration-300">Location</p>
                  <p className="text-gray-600">Serving clients worldwide</p>
                </div>
              </div>
            </div>
            <div className="group bg-blue-50 p-6 rounded-xl transform transition-all duration-300 hover:shadow-md">
              <div className="flex items-center space-x-4">
                <div className="w-12 h-12 rounded-full bg-blue-100 flex items-center justify-center transform transition-all duration-500 group-hover:scale-110 group-hover:bg-blue-200">
                  <Clock className="w-6 h-6 text-blue-600" />
                </div>
                <div>
                  <p className="font-semibold text-blue-900 group-hover:text-blue-800 transition-colors duration-300">Response Time</p>
                  <p className="text-gray-600">Within 24 hours</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ContactPage; 